.navMenu_worker {
    width: 100%;
    box-sizing: border-box;
    padding-left: 15px;
    display: flex;
    overflow: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    margin-bottom: 30px;
    margin-top: 15px;
}
.navMenu_worker::-webkit-scrollbar {
    display: none; /* Скрыть полосу прокрутки в WebKit (Chrome, Safari, Opera) */
}
.navMenu_worker_mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    height: 65px;
    color: #0e9594;
    background-color: #fff;
    border-radius: 10px;
}
.navMenu_worker_mobile:not(:last-of-type) {
    margin-right: 11px;
}
