.menu_main_top {
    width: 95%;
    margin: 20px auto 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu_main_top_title {
    font-weight: bold;
    font-size: 30px;
}
.menu_main_title {
    font-weight: bold;
    font-size: 25px;
    margin-left: 2.5%;
    margin-top: 10px;
}
.adminDetails_item_wrapper {
    width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.adminDetails_item {
    width: 50%;
    font-size: 20px;
    margin: 10px 0;
    display: flex;
}

.adminDetails_item span:first-of-type {
    font-weight: bold;
    margin-right: 5px;
    box-sizing: border-box;
}
.admin_order_titleOfItem {
    margin-top: 10px;
    margin-left: 2.5%;
    font-size: 20px;
    font-weight: bold;
}
