.workerOrders_tile_wrapper {
    width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.workerOrders_tile_order {
    display: flex;
    font-size: 40px;
    font-weight: bold;
}
.workerOrders_tile_order span:first-of-type {
    margin-right: 15px;
}
.workerOrders_tile_status {
    display: flex;

    font-size: 25px;
}
.workerOrders_tile_status span {
    position: relative;
}
.workerOrders_tile_status span:not(:last-of-type) {
    margin-right: 35px;
}
.workerOrders_tile_status span:nth-of-type(2)::after {
    content: '';
    position: absolute;
    top: 1px;
    left: -29px;
    background-color: #81f499;
    width: 25px;
    height: 25px;
    border-radius: 7px;
}
.workerOrders_tile_status span:nth-of-type(3)::after {
    content: '';
    position: absolute;
    top: 1px;
    left: -29px;
    background-color: #78cdd7;
    width: 25px;
    height: 25px;
    border-radius: 7px;
}

/* status modal */
.modalWorker_status {
    border-radius: 10px;
    overflow: hidden;
}
.modalWorker_status_title {
    font-size: 40px;
    font-weight: bold;
    margin: 0 auto;
    margin-bottom: 50px;
}
.modalWorker_status_wrapperButton {
    width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.modalWorker_status_wrapperButton div {
    display: flex;
    align-items: center;
    width: 220px;
    padding: 9px;
    box-sizing: border-box;
    border-radius: 19px;
    font-size: 28px;
    justify-content: center;
    cursor: pointer;
}
.modalWorker_status_wrapperButton div img {
    width: 45px;
    height: 45px;
    margin-right: 20px;
}

.modalWorker_status_wrapperButton div:last-of-type img {
    width: 36px;
    height: 36px;
}

.modalWorker_status_wrapperButton div:nth-of-type(1) {
    border: 5px solid #81f499;
}

.modalWorker_status_wrapperButton div:nth-of-type(2) {
    border: 5px solid #78cdd7;
}
.modalWorker_status_wrapperButton div:nth-of-type(3) {
    border: 5px solid #a5a8b0;
}

/* modal details order  */

.orderWorkerDetails_modal_wrapper {
    background: rgba(165, 168, 176, 0.2);
    border-radius: 15px;
}
.orderWorkerDetails_modal_top {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.orderWorkerDetails_modal_top div {
    display: flex;
    font-size: 24px;
    align-items: center;
}
.orderWorkerDetails_modal_top_bold {
    font-weight: bold;
}

.orderWorkerDetails_modal_top_payment {
    padding: 5px;
    color: #fff;
    background-color: #81f499;
    border-radius: 20px;
}
.orderWorkerDetails_modal_main_head {
    display: flex;
    justify-content: space-between;
    background-color: #a5a8b0;
    border-radius: 15px;
    padding: 10px 15px;
    font-size: 20px;
    color: #fff;
}
.orderWorkerDetails_modal_main_head span {
    width: 15%;
}
.orderWorkerDetails_modal_main_head span:nth-of-type(2) {
    width: 50%;
}
.orderWorkerDetails_modal_main_body {
    padding: 10px 15px;
}
.orderWorkerDetails_modal_main_body_card_prod {
    display: flex;
    justify-content: space-between;
    margin: 15px 0;
}
.orderWorkerDetails_modal_main_body_card_prod span {
    width: 15%;
}
.orderWorkerDetails_modal_main_body_card_prod span:nth-of-type(2) {
    width: 50%;
}

.orderWorkerDetails_modal_main_body_card_addit {
    display: flex;
    margin-left: 10%;
}
.orderWorkerDetails_modal_main_body_card_addit span:first-of-type {
    font-weight: bold;
    margin-right: 10px;
}

@media only screen and (max-width: 500px) {
    .workerOrders_tile_order {
        font-size: 17px;
    }
    .workerOrders_tile_status {
        font-size: 15px;
    }
    .workerOrders_tile_order span:first-of-type {
        margin-right: 5px;
    }
    .workerOrders_tile_status span:nth-of-type(2)::after,
    .workerOrders_tile_status span:nth-of-type(3)::after {
        width: 20px;
        height: 20px;
        left: -23px;
        top: -3px;
    }
    .workerOrders_tile_status span:not(:last-of-type) {
        margin-right: 27px;
    }

    .orderWorkerDetails_modal_top {
        padding: 0;
    }
    .orderWorkerDetails_modal_top div {
        font-size: 12px;
        margin-bottom: 7px;
    }
    .orderWorkerDetails_modal_main_head,
    .orderWorkerDetails_modal_main_body {
        font-size: 12px;
        padding: 5px 10px;
    }
    .modalWorker_status_title {
        font-size: 16px;
        margin-bottom: 30px;
    }
    .modalWorker_status_wrapperButton div {
        display: block;
        width: 100px;
        font-size: 14px;
    }
    .modalWorker_status_wrapperButton div img,
    .modalWorker_status_wrapperButton div:last-of-type img {
        width: 25px;
        height: 25px;
        margin: 0 auto;
        margin-bottom: 5px;
    }
}
