.worker_menu_wrapper {
    display: flex;
    margin: 0 auto;
    height: 100%;
    user-select: none;
    /* position: relative; */
}

.worker_menu_wrapper_header {
    padding: 0 30px;
    display: flex;
    align-items: center;
    height: 100px;
    box-sizing: border-box;
    width: 65%;
}
.worker_menu {
    width: 65%;
}
.worker_menu_wrapper_header :hover {
    opacity: 0.7;
}
.worker_menu_title {
    font-size: 25px;
    margin: 20px 30px;
}
/* basket ================================== */

.worker_basket {
    position: fixed;
    right: 0;
    width: 35%;
    min-width: 360px;
    height: 100vh;
}

.worker_basket_header {
    display: flex;
    height: 108px;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 45px;
    text-align: center;
    background-color: #f2542d;
    border-radius: 45px 0 0 0;
}

.worker_basket_T-head {
    display: flex;
    height: 51px;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
    color: #a5a8b0;
    background-color: #f2e9db;
}

.worker_basket_products {
    background-color: #fff;
    height: calc(100% - 108px - 51px - 210px);
    overflow-y: auto;
    padding: 5%;
    z-index: 1000;
}
.worker_basket_products_card {
    border-bottom: solid #a5a8b0 2px;
    width: 100%;
    margin-top: 15px;
}
.worker_basket_products_card_wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    height: 65px;
    justify-content: space-between;
    margin-bottom: 20px;
}
.worker_basket_products_card_name {
    width: 20%;
}
.worker_basket_products_card_counter {
    display: flex;
    align-items: center;
    background-color: #f2e9db;
    border-radius: 17px;
    font-size: 20px;
    height: fit-content;
}
.worker_basket_products_card_counter span {
    padding: 6px 8px;
}

.worker_basket_products_card_add {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    cursor: pointer;
}
.worker_basket_products_card_add img {
    width: 37px;
    height: 37px;
    margin-right: 10px;
}

.worker_basket_bottom {
    height: 210px;
    background-color: #fff;
    padding: 0 5%;
}

.worker_basket_bottom_text {
    display: flex;
    min-height: 70px;
    align-items: center;
    justify-content: space-between;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 15px;
}
.worker_basket_bottom_button {
    display: flex;

    justify-content: space-between;
}
.worker_basket_bottom_button button {
    font-size: 20px;
    padding: 15px 30px;
    background-color: #a5a8b0;
    border-radius: 15px;
    color: #fff;
    border: none;
}
.worker_basket_bottom_button button:last-of-type {
    background-color: #f2542d;
}
.worker_basket_addition {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    margin: 0 auto;
    border-bottom: solid #a5a8b0 2px;
}

.worker_basket_addition span {
    width: 10%;
    text-align: center;
    font-size: 16px;
    margin-bottom: 10px;
}

.worker_payment_price {
    display: flex;
    justify-content: space-between;
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 30px;
}
.worker_payment_item {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px 0 10px 10px;
    border: 2px solid #a5a8b0;
    border-radius: 7px;
    box-sizing: border-box;
}
.worker_payment_item:not(:first-of-type) {
    margin-top: 10px;
}
.worker_payment_item img {
    width: 40px;
    height: 40px;
    margin-right: 20px;
}
.worker_payment_item span {
    font-size: 28px;
}

@media only screen and (max-width: 1230px) {
    .worker_menu_wrapper_header {
        padding: 0 15px;

        height: 60px;
    }
    .worker_menu_title {
        font-size: 25px;
        margin: 15px;
    }
    .worker_menu_wrapper_header {
        width: calc(100% - 360px);
    }
    .worker_menu {
        width: calc(100% - 360px);
    }
}
@media only screen and (max-width: 500px) {
    .worker_menu_wrapper {
        display: block;
    }
    .worker_basket {
        position: static;
        width: 100%;
    }
    .worker_basket_header {
        border-radius: 0;
    }
}
