.navMenu {
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    top: 73px;
    height: 115px;
    padding-left: 25px;
    display: flex;
    overflow: scroll;
    scrollbar-width: none; /* Скрыть полосу прокрутки в Firefox */
    -ms-overflow-style: none; /* Скрыть полосу прокрутки в IE/Edge */
}
.navMenu::-webkit-scrollbar {
    display: none; /* Скрыть полосу прокрутки в WebKit (Chrome, Safari, Opera) */
}
.navMenu_item {
    min-width: 71px;
    min-height: 115px;
    background-color: #a5a8b0;
    border-radius: 35px;
    margin-right: 38px;
    cursor: pointer;
}

.navMenu_item img {
    margin: 7px auto 11px;
    border-radius: 50%;
    padding: 9px;
    background-color: #f2e9db;
}
.navMenu_item span {
    text-align: center;
    color: #fbfbfb;
    font-size: 13px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
