.search_header {
  width: 100%;
  height: 160px;
  padding: 26px;
  box-sizing: border-box;
  background-color: #f2542d;
  border-radius: 0px 0px 30px 30px;
  margin-bottom: 25px;
}

.search_header_back {
  margin-bottom: 27px;
  height: 26px;
  width: 26px;
}
.search_header_back:hover {
  opacity: 0.8;
}

.search_header input {
  display: block;
  width: 100%;
  height: 47px;
  margin: 0 auto;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  box-sizing: border-box;
  padding-left: 50px;
  padding-top: 3px;
  color: #fff;
}

.search_header input::placeholder {
  color: rgba(243, 237, 227, 1);
}

.search_header label {
  position: relative;
}
.search_header label::after {
  position: absolute;
  content: url(../../style/image/search.png);
  width: 25px;
  height: 25px;
  top: 12px;
  left: 15px;
}
