.navbar {
    margin-top: 20px;
}

/* .navbar a {
    margin: 20px 10px;
} */

.navbar a + div {
    padding: 20px;
    background-color: rgba(165, 168, 176, 1);
    display: none;
}
.navbar div a {
    color: #fff;
}
.navbar a.active {
    color: #fff;
    background-color: rgba(165, 168, 176, 1);
}
.navbar a.active + div {
    display: flex;
}
.navbar a .navbar_burger {
    margin-left: 30px;
    width: 15px;
    height: 10px;
    fill: black;
}
/* .navbar a.active svg {
    fill: #fff;
} */

.nav_aside_submenu {
    flex-direction: column;
    padding: 0 10px;
    align-items: flex-end;
}
.nav_aside_submenu a {
    padding-bottom: 10px;
    margin-right: 40px;
}
.nav_aside_submenu a.active {
    color: #f2542d;
}
.sidenav {
    width: 200px;
    min-width: 200px;
    /* transition: width 0.3s ease-in-out; */
    min-height: 100%;
    background-color: rgba(255, 255, 255, 0.75);
    padding-top: 28px;
}
.sidenavClosed {
    min-height: 100%;
    background-color: rgba(255, 255, 255, 0.75);
    padding-top: 28px;
    /* transition: width 0.3s ease-in-out; */
    width: 60px;
}
.sideitem {
    display: flex;
    align-items: center;
    padding: 10px 10px;
    cursor: pointer;
    color: #b2bac2;
    text-decoration: none;
    overflow: hidden;
}
.sideitem.active {
    background-color: #fff;
}
.sideitem img {
    width: 35px;
    height: 35px;
}
.linkText {
    padding-left: 16px;
}

.menuBtn {
    align-self: center;
    align-self: flex-start;
    justify-self: flex-end;
    color: #b2bac2;
    background-color: transparent;
    border: none;
    cursor: pointer;
}
