.admin_header_wrapper {
    background: linear-gradient(45deg, rgba(255, 255, 255, 0.75), #f3ede3);
    position: relative;
}
.worker-header img {
    width: 40px;
    height: 40px;
}
.worker-header {
    display: flex;
    background-color: #f2542d;
    border-radius: 0 0 50px 50px;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
    margin: 0;
    min-height: 113px;
}
.worker-header div {
    width: 33.3%;
}

.worker-header_date {
    display: flex;
    justify-content: center;
    color: #fff;
    align-items: center;
}
.worker-header_date > *:not(:last-child) {
    margin-right: 20px;
}
.worker-header_options {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.worker-header_options > *:not(:last-child) {
    margin-right: 20px;
}

.worker-header_options span {
    font-size: 24px;
    color: #fff;
}

@media only screen and (max-width: 500px) {
    .worker-header_date {
        flex-direction: column;
    }
    .worker-header_date > *:not(:last-child) {
        margin-right: 0;
    }
}

.adminHeader_user {
    padding: 5px;
    border-radius: 50px;
    background-color: #f2e9db;
    cursor: pointer;
}

.worker-header .modalAdminHeader {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    z-index: 9999;
    margin: 0;
}

.worker-header .modalAdminHeader_dialog {
    position: absolute;
    top: 90px;
    right: 90px;
    width: 190px;
    height: 190px;
    background: white;
    border-radius: 10px;
}
