.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.25);
    animation-name: appear;
    animation-duration: 300ms;
}

.modal_dialog {
    width: 100%;
    max-width: 80%;
    background: white;
    position: relative;
    margin: 0 20px;
    max-height: calc(100vh - 40px);
    /* text-align: left; */
    display: flex;
    border-radius: 10px;
    flex-direction: column;
    overflow: hidden;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: slide-in;
    animation-duration: 0.5s;
}

.modal_header,
.modal_footer {
    display: flex;
    align-items: center;
    padding: 1rem;
}
.modal_header {
    border-bottom: 1px solid #dbdbdb;
    justify-content: space-between;
}
.modal_footer {
    border-top: 1px solid #dbdbdb;
    justify-content: space-between;
}
.modal_footer button:first-of-type {
    padding: 10px 30px;
    background-color: rgba(165, 168, 176, 1);
    color: #fff;
    border-radius: 13px;
    border: none;
}
.modal_footer button:last-of-type {
    padding: 10px 30px;
    background-color: rgba(242, 84, 45, 1);
    color: #fff;
    border-radius: 13px;
    border: none;
}
.modal_close {
    cursor: pointer;
    padding: 1rem;
    margin: -1rem -1rem -1rem auto;
}
.modal_body {
    overflow: auto;
}
.modal_content {
    padding: 1rem;
}

.modal_title {
    margin: 0 auto;
}
.modal.workerOrder .modal_dialog {
    margin: 0;
    max-width: 95%;
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes slide-in {
    from {
        transform: translateY(-150px);
    }
    to {
        transform: translateY(0);
    }
}
