.menuCard {
  display: flex;
  height: 159px;
  width: 335px;
  background: #fff;
  overflow: hidden;
  border-radius: 21px;
}
.menuCard_img {
  width: 131px;
  min-height: 159px;
}
.menuCard_text {
  display: flex;
  flex-direction: column;
  padding: 17px 0 0 9px;
}
.menuCard_text div {
  flex-grow: 1;
}
.menuCard_text div > :first-child {
  font-weight: bold;
  margin-bottom: 3px;
}
.menuCard_text div > :nth-child(2) {
  color: gray;
  margin-bottom: 21px;
}

.menuCard_button {
  width: fit-content;
  color: #fff;
  margin-bottom: 13px;
  padding: 5px 15px;
  background-color: #f2542d;
  /* border: #0e9594 solid 3px; */
  border: none;
  border-radius: 7px;
}
