.worker_menu_cards {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    gap: 68px 40px;
}

.worker-menu_card {
    width: 191px;
    /* height: fit-content; */
    padding: 10px;
    background-color: #a5a8b0;
    box-sizing: border-box;
    border-radius: 29px;
    cursor: pointer;
}

.worker-menu_card_img-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    margin-bottom: 13px;
    width: 169px;
    height: 124px;
    border-radius: 29px;
}
.worker-menu_card_img-container img {
    width: 169px;
    height: 124px;
    border-radius: 14px;
}

.worker-menu_card_text {
    color: #fff;
    font-size: 22px;
    text-align: center;
}
.worker_basket_products_card_addition {
    width: 70%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.worker_basket_products_card_addition:last-of-type {
    margin-bottom: 10px;
}
/* drop =============== */
.worker_basket_drop_wrapper {
    position: relative;
    background-color: #a5a8b0;
    border-radius: 19px;
    color: #fff;
}

.worker_basket_drop_title {
    margin-right: 5px;
}
.worker_basket_drop {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 90px;
    height: 36px;
    border-radius: 46px;
    background-color: #0e9594;
    cursor: pointer;
}
.worker_basket_drop_wrapper.active {
    height: 54px;
}
.rotate__img {
    transform: rotate(180deg);
}
.worker_basket_drop_wrapper.active .worker_basket_drop {
    background-color: #dedede;
}

.worker_basket_drop_select_wrapper {
    position: absolute;
    text-align: center;
    width: 90px;
    background-color: #a5a8b0;
    border-radius: 0 0 17px 17px;
    color: #fff;
}

.worker_basket_drop_select:first-of-type {
    margin-top: 5px;
}
.worker_basket_drop_select:last-of-type {
    border-radius: 19px;
}
.worker_basket_drop_select {
    margin-bottom: 10px;
}
.worker_basket_drop_select:hover {
    opacity: 0.6;
    cursor: pointer;
}

@media only screen and (max-width: 1230px) {
    .worker_menu_cards {
        gap: 30px;
    }
}
