.modal_worker {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    animation-name: appear;
    animation-duration: 300ms;
    z-index: 999;
}

.modal_worker_content {
    width: 100%;
    /* height: 55%; */
    background-color: #f2e9db;
    animation-name: slide-in-product;
    animation-duration: 0.5s;
    /* overflow-y: scroll; */
    border-radius: 30px;
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes slide-in-product {
    from {
        transform: translateY(150px);
    }
    to {
        transform: translateY(0);
    }
}

/* modal product */
.product_amount {
    display: flex;
    margin-top: 28px;
    justify-content: center;
}
.worker_basket_products_card {
    width: fit-content;
}
.product_amount span {
    font-size: 20px;
    padding: 10px;
}
.product_amount span.active {
    border-radius: 10px;
    color: #fff;
    background-color: #0e9594;
}
.product_amount span:not(:last-of-type) {
    margin-right: 24px;
}

.product {
    margin-top: 17px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.product_name {
    font-size: 25px;
    font-weight: bold;
}
.product_description {
    margin-top: 17px;
    color: #8f8f8f;
}

.product_counter {
    display: flex;
    align-items: center;
    background-color: #fff;
    border-radius: 17px;
    font-size: 20px;
    height: fit-content;
}
.product_counter span {
    padding: 6px 8px;
}
.adittions_title {
    margin: 30px 0 20px 0;
    font-size: 18px;
    font-weight: bold;
}
.additions_cards {
    display: flex;
    align-items: flex-end;
    overflow-x: scroll;
    gap: 32px;
    max-width: 100%;
    height: 100px;
    padding-left: 7px;
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.additions_cards::-webkit-scrollbar {
    display: none;
}

.additions_card {
    position: relative;
    background-color: #fff;
    text-align: center;
    padding: 3px 3px 5px 3px;
    border-radius: 10px;
    width: 69px;
    height: 85px;
}
.additions_check_add,
.additions_check_delete {
    position: absolute;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    top: -7px;
    left: -6px;
}
.additions_check_add {
    background-color: rgba(14, 149, 148, 1);
    color: #fff;
}
.additions_check_delete {
    background-color: #fff;
    color: rgba(14, 149, 148, 1);
}
.additions_card img {
    margin: 0 auto;
    width: 47px;
    height: 47px;
}
.additions_card span:not(:first-of-type) {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 12px;
}
.additions_cards_title {
    margin-bottom: 10px;
}
.additions_card span:last-of-type {
    color: #f2542d;
}
.additions_card.active {
    background-color: #0e9594;
    color: #fff;
}
.additions_card.active span:last-of-type {
    color: #fff;
}
.product_price {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 20px;
    margin-top: 35px;
    margin-bottom: 22px;
}

.product_price span:not(:first-of-type) {
    color: #f2542d;
    font-size: 20px;
}
.product_price span:nth-of-type(2) {
    font-size: 23px;
    margin: 0 5px;
    font-weight: bold;
}
.product_buttuon {
    display: block;
    margin: 0 auto;
    color: #ffffff;
    font-size: 20px;
    padding: 5px 30px;
    background-color: #f2542d;
    border: 3px solid #0e9594;
    border-radius: 10px;
    margin-bottom: 35px;
}
