.worker_main_cards {
    display: flex;
    width: 90%;
    margin: 0 auto;
    flex-wrap: wrap;
    justify-content: center;
    gap: 68px;
    margin-top: 113px;
}

.worker-main_card {
    width: 373px;
    height: 258px;
    padding: 10px;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 29px;
}
.worker-main_card_img-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0e9594;
    margin-bottom: 13px;
    width: 338px;
    height: 163px;
    border-radius: 29px;
    margin: 0 auto;
}
.worker-main_card_img-container img {
    width: 118px;
    height: 118px;
}

.worker-main_card_text {
    color: #0e9594;
    font-size: 40px;
    text-align: center;
    margin-top: 13px;
}

@media only screen and (max-width: 500px) {
    .worker-main_card {
        width: 273px;
        height: 200px;
        padding: 10px;
    }
    .worker-main_card_img-container {
        width: 95%;
        height: 130px;
    }
}
