body {
    margin: 0;
    background: #f3ede3;
}
main {
    width: 100%;
}
a {
    display: block;
    text-decoration: none;
    color: black;
}
li {
    list-style: none;
}
input {
    padding-left: 10px;
}
ul {
    margin: 0;
    padding: 0;
}
span {
    display: block;
}
img {
    display: block;
    object-fit: cover;
}

h2 {
    margin: 0;
}

input,
select,
button,
textarea {
    margin: 0;
    font-size: 100%;
    outline: none;
}
input[type='text'],
input[type='password'],
textarea {
    padding: 0;
}
input[type='checkbox'] {
    vertical-align: bottom;
}
input[type='radio'] {
    vertical-align: text-bottom;
}
