/* Icon   =====================================  */
.basketIcon {
    display: block;
    position: fixed;
    top: 90%;
    right: 5%;
}
.basketIcon div {
    position: relative;
}
.basketIcon_count {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 26px;
    height: 26px;
    top: -2px;
    right: 9px;
    color: #fff;
    border-radius: 50%;
    background-color: #0e9594;
}
.basketIcon img {
    width: 73px;
    height: 72px;
}

/* Basket header ====================== */

.basket_header {
    display: flex;
    align-items: center;
    padding: 32px 28px;
}
.basket_header img {
    width: 26px;
    height: 26px;
}
.basket_header span {
    text-align: center;
    flex: 1;

    font-size: 25px;
    font-weight: bold;
}

/* Basket card ====================== */
.basket_card {
    width: 337px;
    height: 267px;
    box-sizing: border-box;
    padding: 12px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 10px;
}
.basket_card_product {
    display: flex;
}
.basket_card_product img {
    border-radius: 17px;
    width: 114px;
    height: 114px;
}
.basket_card_text {
    margin-left: 11px;
}
.basket_card_text div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
